@use 'global' as *;

.top-mv {
	width: 100%;
	height: 100vh;
	min-height: rem(400);
	position: relative;
	@include mq("md") {
		min-height: rem(500);
	}
}

// .top-mv__inner {
	// width: 100%;
	// height: inherit; 
	//子要素に影響されるため何も指定しなくても問題なし
// }

.top-mv__title-wrap {
	position: absolute;
	z-index: 10;
	// top: 44%;
	// left: 17%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: $white;
	text-align: center;
  // color: $green;
  animation: fadeInOut 3s forwards;

	
	@include mq("md") {
		// top: 41.5%;
		// left: 32.8%;
		top: 50%;
  	left: 50%;
		transform: translate(-50%, -50%);
	}
}

