.top-mv__title {
	font-size: rem(40);
	line-height: calc(48/40);
	letter-spacing: 0.54em;
	font-weight: $regular;
	font-family: "Lato", sans-serif;
	text-transform: uppercase;
	text-indent: 0.555em;
	@include mq("md") {
		font-size: rem(80);
		line-height: calc(100/80);
	}
}

.top-mv__text {
	font-size: rem(20);
	line-height: calc(33/20);
	// font-family: "Gotu", sans-serif;
	font-weight: $regular;
	// margin-right: rem(24);
	@include mq("md") {
		// margin-right: rem(45);
	}
}


