.button {
  position: relative;
  display: inline-block;
  margin-right: 0.25rem;
  margin-bottom: 0.3125rem;
  padding: 1rem 3.125rem 1rem 0;
  width: 12.625rem;
  height: 3.4375rem;
  font-size: 0.875rem;
  line-height: 1.5714285714;
  font-weight: 400;
  background-color: #408F95;
  color: #fff;
  border: 0.0625rem solid #408F95;
  text-align: center;
  align-items: center;
  z-index: 1;
}

.button-white {
  padding: 1rem 3.625rem 1rem 0;
  background-color: #fff;
  color: #408F95;
  border: 0.0625rem solid #fff;
}

.button__arrow1 {
  position: absolute;
  top: 50%;
  right: 2.5rem;
  height: 0.0625rem;
  background-color: #fff;
  width: 2.5rem;
}

.button__arrow1-white {
  background-color: #408F95;
}

.button__arrow2 {
  position: absolute;
  top: 45%;
  right: 2.5rem;
  width: 0.5rem;
  height: 0.0625rem;
  transform: rotate(40deg);
  background-color: #fff;
}

.button__arrow2-white {
  background-color: #408F95;
}

.button::before {
  content: "";
  position: absolute;
  width: 12.625rem;
  height: 3.375rem;
  top: 0.25rem; /* ずらした位置 */
  left: 0.25rem;
  right: 0;
  bottom: 0;
  border: 0.0625rem solid #408F95; /* 枠線の色を指定 */
  pointer-events: none; /* ボタンの操作を妨げないようにする */
  z-index: -2;
}

.button-white::before {
  border: 0.0625rem solid #fff;
}

.button::after {
  content: "";
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button-white::after {
  background-color: #408F95;
}

.button:hover {
  color: #408F95;
  opacity: 1;
}

.button-white:hover {
  color: #fff;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button:hover .button__arrow1,
.button:hover .button__arrow2 {
  background-color: #408F95;
}

.button-white:hover .button__arrow1,
.button-white:hover .button__arrow2 {
  background-color: #fff;
}

